@import url('https://fonts.googleapis.com/css2?family=Joan&family=Nuosu+SIL&family=Josefin+Sans:ital,wght@1,700&family=Kdam+Thmor+Pro&family=Odibee+Sans&family=Oleo+Script+Swash+Caps&family=Radio+Canada&family=Tapestry&family=Water+Brush&family=ZCOOL+KuaiLe&display=swap');


.app{
 

  font-family: 'Nuosu SIL', serif;

color: #ffd6ff;
 text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  justify-items: center;
  gap: 30px;

}


input{
border:3px solid #1565c0;
color: #1565c0;
font-weight: 700;
border-radius: 10px;
height: 30px;
}


.container{
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 50px;
 border-radius: 30px;
 background-color: #2196f3;
 background: linear-gradient(to right, #b7ceeb, #2196f3 50%, #2e6bf7 95%);
 box-shadow: 10px 10px 10px #879fd6;
width: 700px;
height: 350px;
}

.description{
font-size: 2rem;
}

.location{
  font-size: 2rem;
}

.temp{
  font-size: 2rem;
  font-weight: 700;
 }
 .humidity{
  font-size: 1.5rem;
  display: flex;
  justify-content: space-between;
 }

 img{
  height: 300px;
  width: 7px;
  
 }


 .column1, .column2{
max-width: 46%;
 }


 .column1{
  display: flex;
  flex-direction: column;
  gap:2px;
  height: 350px;
 }

 .column2{
height: 300px;
 }





